<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <a href="/">
          <img src="../assets/logo.png" alt="logo.png" />
        </a>
      </div>
      <div class="logo_name">
        <span>KY-Library</span>
      </div>
      <!--  -->
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginFormRules"
        class="login_form"
      >
        <!-- accout -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            prefix-icon="el-icon-user-solid"
            placeholder="user accout"
          ></el-input>
        </el-form-item>
        <!-- password -->
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            prefix-icon="el-icon-lock"
            placeholder="password"
          ></el-input>
        </el-form-item>
        <a class="a_register" href="/#/register"></a>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        username: "",
        password: "",
      },
      // 
      loginFormRules: {
        //
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: " 6  15 ",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    // 
    login() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        var targetURL = this.loginForm.username + "/" + this.loginForm.password;
        const { data: res } = await this.$http({
          method: "get",
          url: "/LogIn/" + targetURL,
        });
        console.log(res);
        if (res.data.length === 0) return this.$message.error("");
        this.$message.success({
          message: "",
        });
        // 1tokensessionStorage
        //  - API
        //  - token tokensessionStorage
        window.sessionStorage.setItem("token", res.data.token);
        // 2  /home
        // this.$router.push("/home");
      });
    },
    // 
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields();
    },
  },
};
</script>

