<template>
  <div class="register_container">
    <div class="register_box">
      <!--  -->
      <div class="avatar_box">
        <a href="/">
          <img src="../assets/logo.png" alt="logo.png" />
        </a>
      </div>
      <!-- logo -->
      <div class="logo_name">
        <span>KY-Library</span>
      </div>
      <!--  -->
      <el-form
        ref="registerFormRef"
        :model="registerForm"
        :rules="registerFormRules"
        class="register_form"
      >
        <!-- username -->
        <el-form-item prop="username">
          <el-input
            v-model="registerForm.username"
            prefix-icon="el-icon-user-solid"
            placeholder="username"
          ></el-input>
        </el-form-item>
        <!-- password -->
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="registerForm.password"
            prefix-icon="el-icon-lock"
            placeholder="password"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="checkPw">
          <el-input
            type="password"
            prefix-icon="el-icon-lock"
            v-model="registerForm.checkPw"
            placeholder="check password"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="register('registerFormRef')"
            ></el-button
          >
          <el-button type="info" @click="resetregisterForm('registerFormRef')"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    //   
    var validateUserName = (rule, value, callback) => {
      //   if (value === "") {
      //     callback(new Error(""));
      //   } else {
      //     if (this.registerForm.username !== "") {
      //       this.$refs.registerFormRef.validateField("username");
      //     }
      //     callback();
      //   }
    };
    // 
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (this.registerForm.checkPw !== "") {
        this.$refs.registerFormRef.validateField("checkPass");
      } else {
        callback();
      }
    };
    // 
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.registerForm.password) {
        callback(new Error("!"));
      }
    };

    return {
      registerForm: {
        username: "",
        password: "",
        checkPw: "",
      },
      //   
      registerFormRules: {
        // username: [{ validator: validateUserName, trigger: "blur" }],
        // password: [{ validator: validatePass, trigger: "blur" }],
        // checkPw: [{ validator: validatePass2, trigger: "blur" }],
      },
    };
  },
  methods: {
    //   
    register(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http({
          method: "post",
          url: "/register",
          data: {
            username: "root5",
            password: "123456",
          },
        });
        console.log(res);
      });
    },
    // 
    resetregisterForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style lang="less" scoped>
.register_container {
  height: 100%;
  background-image: url("../assets/bg.jpg");
  background-size: 100% 100%;
}

.register_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.avatar_box {
  height: 130px;
  widows: 130px;
  border-bottom-right-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #2b4b6b;
  position: absolute;
  left: 0;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.3);
  img {
    width: 100%;
    height: 100%;
    border-bottom-right-radius: 50%;
  }
}

.logo_name {
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
  text-align: center;
  font-size: 30px;
  margin: 5px;
}

.register_form {
  bottom: 0;
  width: 100%;
  padding: 28px 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
